<template>
  <el-row :gutter="20">
    <el-col v-for="(item, index) in data" :key="index" :span="6">
      <div :class="index == selectIndex ? 'card active' : 'card'">
        <div class="rect"></div>
        <i class="el-icon-s-grid"></i>
        <span class="card-value">{{item.value}}</span>
        <span>{{item.name}}</span>
      </div>
    </el-col>
  </el-row>
</template>
<script>
export default {
  name: "TOPNList",
  props: {
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      selectIndex: 0
    }
  },
  methods: {
    autoEvent() {
      if (this.data) {
        this.selectIndex++;
        this.selectIndex = this.selectIndex > this.data.length - 1 ? 0 : this.selectIndex;
      }
    }
  },
  mounted() {
    this.autoEvent();
    setInterval(this.autoEvent, 10000);
  }
}
</script>
