<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "PieChart",
  props: {
    title: { type: String, default: "标签" },
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      index: 0,
      chart: null,
      autoIntervalId: 0
    }
  },
  watch: {
    data(val) {
      if (this.chart) {
        this.chart.setOption(this.getOptions(val));
        //设置默认选中高亮部分
        this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: this.index });
      }
    }
  },
  methods: {
    getOptions(list) {
      const colors = this.$colors.slice(2, this.$colors.length - 1);
      // 指定图表的配置项和数据
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: "{d}% <br>{b}: {c}"
        },
        color: colors,
        legend: {
          orient: 'vertical',
          padding: [5, 10],
          itemGap: 20,
          itemWidth: 15,
          itemHeight: 15,
          right: 15,
          y: 'center',
          textStyle: {
            fontSize: '16',
            color: '#FFFFFF'
          },
          data: []
        },
        series: [{
          name: this.title,
          type: 'pie',
          radius: ['70%', '85%'],
          center: ['40%', '50%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              formatter: '{a|{a}}',
              position: 'center',
              rich: {
                a: {
                  fontSize: '35',
                  color: '#30c792'
                }
              }
            },
            emphasis: {
              show: true,
              textStyle: {
                color: '#FFF',
                fontSize: '30',
                formatter: '{c}台',
              }
            }
          },
          data: list || []
        }]
      };
      if (list) {
        option.legend.data = list.map(item => item.name);
      }
      return option;
    },
    addAutoEvent() {
      this.autoIntervalId = setInterval(() => {
        this.chart.dispatchAction({ type: 'downplay', seriesIndex: 0, dataIndex: this.index });
        this.index++;
        this.index = this.index >= this.getOptions(this.data).series[0].data.length ? 0 : this.index;
        this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: this.index });
        this.chart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: this.index });
      }, 5000);
    },
    closeAutoEvent() {
      if (this.autoIntervalId) {
        clearInterval(this.autoIntervalId);
      }
    }
  },
  mounted() {
    const that = this;
    this.chart = this.$echarts.init(this.$refs.chart);

    this.chart.setOption(this.getOptions(this.data));

    //设置默认选中高亮部分
    this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: that.index });
    // 当鼠标移入时，如果不是第一项，则把当前项置为选中，如果是第一项，则设置第一项为当前项
    this.chart.on('mouseover', function (e) {
      that.closeAutoEvent();
      that.chart.dispatchAction({ type: 'downplay', seriesIndex: 0, dataIndex: that.index });
      that.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: e.dataIndex });
    });

    //当鼠标离开时，把当前项置为选中 
    this.chart.on('mouseout', function (e) {
      that.index = e.dataIndex;
      that.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: that.index });
      that.addAutoEvent();
    });

    this.addAutoEvent();
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 480px;
  height: 160px;
}
</style>
