<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "ServerStatusChart",
  mounted() {
    const chart = this.$echarts.init(this.$refs.chart);
    var data = [{
      name: 'CPU',
      value: 100
    }, {
      name: '内存',
      value: 50
    }];

    var titleArr = [];
    var seriesArr = [];
    var colors = [['#389af4', '#dfeaff'], ['#ff8c37', '#ffdcc3'], ['#ffc257', '#ffedcc'], ['#fd6f97', '#fed4e0'], ['#a181fc', '#e3d9fe']]
    data.forEach(function (item, index) {
      titleArr.push({
        text: item.name,
        left: index * 50 + 24 + '%',
        top: '58%',
        textAlign: 'center',
        textStyle: {
          fontWeight: 'normal',
          fontSize: '22',
          color: '#fff',
          textAlign: 'center',
        },
      });

      seriesArr.push({
        name: 'border',
        type: 'pie',
        clockWise: false,
        radius: ['88%', '90%'],
        center: [index * 50 + 25 + '%', '50%'],
        itemStyle: {
          normal: {
            color: '#121f44',
            shadowBlur: 0,
            label: { show: false },
            labelLine: { show: false },
          }
        },
        hoverAnimation: false,
        data: [{
          value: 100,
          name: 'normal1',
        }, {
          value: 0,
          name: 'invisible',
          itemStyle: {
            normal: {
              color: 'rgba(0,0,0,0)',
              label: { show: false },
              labelLine: { show: false }
            },
            emphasis: {
              color: 'rgba(0,0,0,0)'
            }
          }
        }]
      }, {
        name: 'track',
        type: 'pie',
        clockWise: false,
        radius: ['80%', '85%'],
        center: [index * 50 + 25 + '%', '50%'],
        itemStyle: {
          normal: {
            color: colors[index][1],
            shadowColor: colors[index][1],
            shadowBlur: 0,
            label: { show: false },
            labelLine: { show: false },
          }
        },
        hoverAnimation: false,
        data: [{
          value: 100,
          name: 'normal2',
        }, {
          value: 20,
          name: 'invisible',
          itemStyle: {
            normal: {
              color: 'rgba(0,0,0,0)',
              label: { show: false },
              labelLine: { show: false }
            },
            emphasis: {
              color: 'rgba(0,0,0,0)'
            }
          }
        }]
      });

      seriesArr.push({
        name: item.name,
        type: 'pie',
        clockWise: false,
        radius: ['70%', '85%'],
        startAngle: 90,
        itemStyle: {
          normal: {
            color: colors[index][0],
            shadowColor: colors[index][0],
            shadowBlur: 0,
            label: {
              show: false
            },
            labelLine: {
              show: false
            },
          }
        },
        hoverAnimation: false,
        center: [index * 50 + 25 + '%', '50%'],
        data: [{
          value: item.value,
          label: {
            normal: {
              formatter: function (params) {
                return params.value + '%';
              },
              position: 'center',
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold',
                color: colors[index][0]
              }
            }
          },
        }, {
          value: 20,
          name: 'invisible',
          itemStyle: {
            normal: {
              color: 'rgba(0,0,0,0)',
              label: { show: false },
              labelLine: { show: false }
            },
            emphasis: {
              color: 'rgba(0,0,0,0)'
            }
          }
        }]
      })
    });

    var option = {
      title: titleArr,
      series: seriesArr,
    }

    chart.setOption(option);
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 480px;
  height: 180px;
}
</style>