<template>
  <div ref="chart" class="chart"></div>
</template>
<script>
export default {
  name: "LineChart",
  props: {
    names: { type: Array, default: () => [] },
    data: { type: Array, default: () => [] }
  },
  data() {
    return {
      chart: null,
    }
  },
  watch: {
    data(val) {
      if (this.chart) {
        this.chart.setOption(this.getOptions(val));
      }
    }
  },
  methods: {
    getOptions() {
      if (!this.names || !this.data) {
        return;
      }

      const colors = this.$colors.slice(2, this.$colors.length - 1);
      var option = {
        legend: {
          data: [],
          icon: 'rect',
          top: 0,
          right: 25,
          itemGap: 15,
          itemWidth: 10,
          itemHeight: 10,
          textStyle: {
            fontSize: '16',
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            },
          }
        },
        grid: {
          top: 30,
          left: 20,
          right: 30,
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            textStyle: {
              fontSize: 14,
              color: '#FFFFFF',
            }
          },
          axisTick: {
            show: false
          },
          boundaryGap: false,
          nameTextStyle: {
            padding: [24, 0, 0, 0],
            color: '#00c5d7'
          },
          nameGap: 0,
          data: this.names || []
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: false
          },
          axisTick: {
            length: 0 // 刻度线的长度
          },
          splitLine: {
            lineStyle: {
              color: ["#051d5f"],
              width: 1,
              type: 'dotted'
            }
          },
          axisLabel: {
            textStyle: {
              fontSize: 14,
              color: '#ffffff'
            }
          }
        },
        series: []
      }

      this.data.forEach((item, index) => {
        option.legend.data.push(item.name);
        var series = {
          name: item.name,
          type: 'line',
          smooth: true,
          symbol: 'circle',
          stack: 100,
          symbolSize: 4,
          itemStyle: {
            normal: {
              color: colors[index],
              borderColor: colors[index],
              label: {
                show: true
              },
              areaStyle: {
                type: 'default',
                opacity: 0.85
              }
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 18,
                color: '#ffffff'
              },
              areaStyle: {
                type: 'default',
                opacity: 0.75
              }
            }
          },
          data: item.data
        };
        option.series.push(series);
      });
      return option;
    }
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.chart);
    var option = this.getOptions();
    this.chart.setOption(option);

    var index = 0;
    setInterval(() => {
      if (!this.names) {
        return;
      }
      index++;
      index = index >= this.names.length ? 0 : index;
      // const index = Math.floor(Math.random() * this.names.length);
      this.chart.dispatchAction({ type: 'downplay', seriesIndex: 0 });
      this.chart.dispatchAction({ type: 'highlight', seriesIndex: 0, dataIndex: index });
      this.chart.dispatchAction({ type: 'showTip', seriesIndex: 0, dataIndex: index });
    }, 5000);
  }
}
</script>
<style lang="stylus" scoped>
.chart {
  width: 100%;
  height: 240px;
}
</style>